
import api from '../api/api'
import env from '../config/env'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import ViewBox from 'vux/src/components/view-box/index.vue'

// 由于style目录下有重名文件，所以这里还是要指定文件后缀
import '../style/iconfont.css'

export default {
    name: 'main',
    data() {
        return {
            // 选中tab的index
            selectedIndex: 0,
        }
    },
    mounted() {
        //this.initData()
    },
    created() {
        // 初始化的时候获截取路由的path字符串，改变对应tab的颜色
        const routePath = this.$store.state.route.path.split(/\//) && this.$store.state.route.path.split(/\//)[1];
        if (routePath === 'shop') {
            this.selectedIndex = 0
        } else if (routePath === 'user') {
            this.selectedIndex = 1
        } else if(routePath === 'notice') {
            this.selectedIndex = 2
        } else {
            this.selectedIndex = sessionStorage.tabIndex
        }
    },
    methods: {
        handler(routerName) {
            // 记录点击tab index
            sessionStorage.tabIndex = this.selectedIndex
            this.$router.replace({
                path: "/" + routerName
            })
        }
    },
    components: {
        Tabbar,
        TabbarItem,
        ViewBox
    }
}



